body {
  background-color: rgba(205, 205, 205, 0.247);
  
}

.ProductCard {
  display: flex;
  justify-content: center;
  /* align-items: center; */
  flex-direction: column;
  /* flex-wrap: wrap; */
}


.products {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  text-align: center;
  margin: 2rem;
}

/* .products:last-of-type(div) {
  align-self: flex-start;
  justify-self: flex-start;
} */

.product {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: start;
  width: 25rem;
  height: 25rem;
  flex-direction: column;
  background-color: white;
  margin-bottom: 20px;
  padding: 30px;
}
.product button {
  cursor: pointer;
}

.product button:hover {
  background-color: rgb(222, 244, 56);
}

img {
  width: 10rem;
  height: 200px;
  object-fit: scale-down;
  margin-bottom: 20px;
}

.product p {
  width: 20rem;
  flex-wrap: wrap;
}

.product button {
  border: 0px;
  padding: 10px;
}

.product:hover {
  box-shadow: 16px 7px 48px -33px rgba(0, 0, 0, 0.75);
  transition: 0.2s;
  transition-timing-function: ease-in-out 1s;
}

.product img:hover {
  transition: 0.2s;
  transition-timing-function: ease-in-out 1s;
  transform: scale(1.1);
}

.categorySelector {
  display: flex;
  justify-content: center;
  
  margin-top: 50px;
  /* margin-left: 110px; */
}




@media (max-width:1000px){
  .ProductCard {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
  
}
.categorySelector{
  margin-left:0px;
}
.product{
  width: 10rem;
  height: 18rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: start;
  flex-direction: column;
  background-color: white;
  margin-bottom: 20px;
  padding: 30px;
  
}
.product img{
  width: 4rem;
  height: 4rem;
  object-fit: scale-down;
}
.product p{
  width: 10rem;
  height: 5rem;
  
  
  margin-bottom: 1rem;
  overflow: hidden;
}

.product button {
  margin-top: 20px;
}
}

.categorySelector select {
  border: 0px;
  padding: 20px;
}


@media (max-width:500px){
  
  .categorySelector {
  display: flex;
  justify-content: flex-start;
  /* margin-top: 30px; */
  margin-left: 0px;
}

  .ProductCard {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
  
  }
  .product {
    max-width:6rem;
    height: 17rem;
    margin-top: 20px;
  }
  .product img {
    width:2rem;
  }
  .product p{
    width:100%;
    font-size: 14px;
    overflow: hidden;
  }
}
